<!-- 预警弹窗 -->
<template>
    <div>
        <Modal :value="isShow" :title="'预警'" width="400" :mask-closable="true" @on-cancel="close()">
        <div class="" style="padding:0 10px;">
            <div class="cloa pb5 mb5" style="border-bottom:1px solid #333;">条件</div>
            <Form :model="form" :label-width="100" class="cloa">
                <FormItem label="价格涨到">
                    <Input v-model="form.maxPrice" type="number" placeholder="请输入" class="w150 mr10"></Input>元
                </FormItem>
                <FormItem label="价格跌到">
                    <Input v-model="form.minPrice" type="number" placeholder="请输入" class="w150 mr10"></Input>元
                </FormItem>
                <FormItem label="日涨跌幅达到">
                    <Input v-model="form.percent" type="number" placeholder="请输入" class="w100 mr10"></Input>%
                </FormItem>
                <!-- <FormItem label="5分钟涨幅">
                    <Input v-model="form.password" type="number" placeholder="请输入" class="w100 mr10"></Input>%
                </FormItem>
                <FormItem label="5分钟跌幅">
                    <Input v-model="form.password" type="number" placeholder="请输入" class="w100 mr10"></Input>%
                </FormItem>
                <FormItem label="成交量突破">
                    <Input v-model="form.password" type="number" placeholder="请输入" class="w150 mr10"></Input>元
                </FormItem> -->
            </Form>
            <div class="cloa pb5 mb5 " style="border-bottom:1px solid #333;">频率</div>
            <Form :model="form" :label-width="100" class="cloa">
                <FormItem label="提醒频率">
                    <!-- <RadioGroup v-model="form.mode_2" >
                        <Radio label="mode_21">每日一次</Radio>
                        <Radio label="mode_22">仅提醒一次</Radio>
                    </RadioGroup> -->
                    <Select v-model="form.rate" class="w100" style="width:auto;" placeholder="请选择">
                        <Option :value="1">每日一次</Option>
                        <Option :value="0">仅提醒一次</Option>
                    </Select>
                </FormItem>
            </Form>
            <!-- <div class="cloa pb5 mb5 " style="border-bottom:1px solid #333;">异动</div>
            <div class="cloa pb5 mb5 " style="border-bottom:1px solid #333;">技术指标</div>
            <div class="cloa pb5 mb5 " style="border-bottom:1px solid #333;">技术形态</div> -->
        </div>
        <div slot="footer" class="center">
            <Button type="primary" style="min-width:80px;" @click="confirm">确认</Button>
            <!-- <Button style="margin-left: 15px;min-width:120px;" @click="form={}">重置</Button> -->
        </div>
    </Modal>
    </div>
</template>

<script>
export default {
    name: 'WebWarnPop',
    model:{
        prop:'isShow',
        event:'change'
    },
    props: {
        //连接钱包弹窗，是否显示
        isShow:{
            type:Boolean,
            default:false
        },
        area:{
            type:Object,
            default:()=>{}
        }
    },
    data() {
        return {
            form:{
            },
        };
    },
    mounted() {
        
    },
    methods: {
        confirm(){
            // this.$emit('confirm',this.form)
            let d={
                ...this.form,
                // startTime:this.$dayjs(this.form.startTime).format('YYYY-MM-DD HH:mm:ss'),
                // endTime:this.$dayjs(this.form.endTime).format('YYYY-MM-DD HH:mm:ss'),
                // push:this.$dayjs(this.form.push).format('YYYY-MM-DD HH:mm:ss'),
                // type:'enterprise',//enterprise：企业，mechanism：机构
            }
            // if(!d.mobile){
            //     this.$Message.error('请输入联系人手机号!');
            //     return;
            // }
            this.$api.price_alert_create(d).then(res=>{
                this.$Message.success('提交成功!');
            });
            this.close();
        },
        //关闭
        close(){
            this.$emit("change",false);
        },
    },
};
</script>

<style lang="less" scoped>
:deep(.ivu-modal-header-inner){
    color:#ccc;
}
:deep(.ivu-form-item){
    margin-bottom: 10px;
}
</style>